@use "../variables" as v;
@use "../mixins" as m;

.booking {
  position: relative;

  .container {
    padding: 50px 11.93%;

    @include m.vp-1023 {
      padding: 100px 50px;
    }

    @include m.vp-767 {
      padding: 65px 15px;
    }
  }

  &__content {
    position: relative;
    z-index: 1;

    display: grid;
    grid-template-columns: repeat(2, minmax(10px, 1fr));
    column-gap: 10.54%;
    row-gap: 60px;

    @include m.vp-1023 {
      grid-template-columns: repeat(1, minmax(10px, 1fr));
    }

    @include m.vp-767 {
      row-gap: 40px;
    }
  }

  &__text {
    place-self: center;

    h2 {
      margin: 0;

      font-size: 40px;
      line-height: 60px;

      @include m.vp-1023 {
        text-align: center;

        font-size: 34px;
        line-height: 51px;
      }

      @include m.vp-767 {
        font-size: 24px;
        line-height: 31px;
      }
    }

    p {
      margin: 30px 0 0;

      font-size: 18px;
      line-height: 27px;
    }

    @include m.vp-767 {
      font-size: 16px;
      line-height: 22px;
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    img {
      object-fit: cover;
      object-position: bottom;

      height: 100%;
      width: 100%;
    }
  }

  &__form-wrapper {
    place-self: center;

    padding: 50px;
    width: 403px;

    color: v.$color-default-white;

    background-color: v.$color-default-black;
    box-shadow: 0 4px 45px 0 rgba(1, 28, 64, 0.15);

    @include m.vp-767 {
      padding: 40px 30px;
      width: 100%;
    }

    h3 {
      margin: 0;

      font-weight: 500;
      font-size: 18px;
      line-height: 27px;

      @include m.vp-767 {
        line-height: 25px;
      }
    }

    .custom-toggle__label {
      font-size: 10px;
      line-height: 13px;
    }

    //.custom-input--no-label {
    //  span {
    //    position: absolute;
    //
    //    width: 1px;
    //    height: 1px;
    //    margin: -1px;
    //    padding: 0;
    //    overflow: hidden;
    //
    //    white-space: nowrap;
    //
    //    border: 0;
    //
    //    clip: rect(0 0 0 0);
    //    clip-path: inset(100%);
    //  }
    //
    //  input {
    //    margin-left: 0;
    //  }
    //}
  }

  &__form {
    display: grid;
    grid-template-columns: repeat(1, minmax(10px, 1fr));
    gap: 20px;

    margin-top: 35px;
  }
}
