}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: relative;
  padding: 35px 0;
  height: 650px;
  color: #f9fbfd;
  line-height: 20px;
  font-weight: 500;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  background-color: #87cefa;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .header {
    padding: 30px 0;
    height: 638px;
    font-size: 14px;
    line-height: 17px;
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 25px 0;
    height: 500px;
    font-size: 18px;
    line-height: 22px;
  }
}
.header .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
.header a {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header a:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.header a.focus-visible:focus {
  opacity: 0.6;
}
.header__logo {
  z-index: 1;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 42px;
    height: 22px;
  }
}
.header__logo svg {
  display: block;
  width: 100%;
  height: 100%;
}
.header .header__hero {
  position: relative;
  z-index: 1;
  padding-top: 210px;
  padding-left: 198px;
  max-width: 100%;
}
@media (max-width: 1023px) {
  .header .header__hero {
    padding-top: 230px;
    padding-left: 45px;
  }
}
@media (max-width: 767px) {
  .header .header__hero {
    padding-top: 143px;
    padding-left: 15px;
  }
}
.header .header__hero h1 {
  margin: 0;
  font-size: 56px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .header .header__hero h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .header .header__hero h1 {
    font-size: 36px;
    line-height: 45px;
  }
}
.header .header__hero p {
  margin: 30px 0 0;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .header .header__hero p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .header .header__hero p {
    margin-top: 25px;
    font-size: 16px;
    line-height: 22px;
  }
}
.header__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header__bg img {
  object-fit: cover;
  object-position: bottom;
  height: 100%;
  width: 100%;
}

.header__burger {
  display: none;
  position: absolute;
  z-index: 4;
  top: 0;
  right: 0;
  padding: 0;
  width: 48px;
  height: 48px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__burger:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.header__burger.focus-visible:focus {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .header__burger {
    display: block;
  }
}
.header__burger span,
.header__burger span::after,
.header__burger span::before {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  transition: transform 0.2s ease-in-out;
}
.header__burger span::before {
  transform: translate(0, -4px);
}
.header__burger span::after {
  transform: translate(0, 4px);
}

.is-open {
  overflow: unset;
}
.is-open .header__burger span,
.is-open .header__burger span::after,
.is-open .header__burger span::before {
  background-color: #011c40;
}
.is-open .header__burger span::after {
  display: none;
}
.is-open .header__burger span::before {
  transform: translate(0, 0) rotate(90deg);
}
.is-open .header__burger span {
  transform: translate(0, 0) rotate(45deg);
}
@media (max-width: 767px) {
  .is-open .header__logo {
    opacity: 0;
  }
}
.is-open .header__bg::after {
  content: "";
  position: absolute;
  z-index: 2;
  top: 0;
  display: block;
  width: 100vw;
  height: 100vh;
}
@media (max-width: 767px) {
  .is-open .header__bg::after {
    background-color: rgba(0, 43, 102, 0.5);
  }
}

.main-nav {
  z-index: 3;
  transition: transform 0.2s ease-in-out;
}
.main-nav__logo {
  display: none;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  gap: 33px;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
    gap: 10px;
    padding-top: 33px;
    overflow: auto;
  }
}
.main-nav__link {
  display: block;
  color: inherit;
}
@media (max-width: 767px) {
  .main-nav__link {
    padding: 10px 40px 8px 0;
  }
}
.main-nav__link.is-active {
  opacity: 0.6;
}

@media (max-width: 767px) {
  header .main-nav {
    position: fixed;
    top: 0;
    right: 0;
    padding: 20px 30px 20px 15px;
    height: 100vh;
    /* stylelint-disable-next-line */
    height: calc(100 * var(--vh, 1vh));
    color: #011c40;
    background-color: #f9fbfd;
    transform: translate(100%);
    overflow: hidden;
  }
}

@media (max-width: 767px) {
  .is-open .main-nav {
    transform: translate(0);
    overflow: unset;
  }
}
.is-open .main-nav__logo {
  display: block;
}

.intro {
  background-color: #f9fbfd;
}
.intro .container {
  display: grid;
  grid-template-columns: repeat(2, minmax(10px, 1fr));
  gap: 90px;
  padding: 120px 103px 145px;
  height: 566px;
}
@media (max-width: 1023px) {
  .intro .container {
    gap: 30px;
    padding: 100px 45px;
    height: 464px;
  }
}
@media (max-width: 767px) {
  .intro .container {
    grid-template-columns: repeat(1, minmax(10px, 1fr));
    padding: 65px 15px 85px;
    height: unset;
  }
}
.intro .container > * {
  place-self: center;
}
.intro .container div h2,
.intro .container div p {
  padding: 0;
}
.intro .container div h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .intro .container div h2 {
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .intro .container div h2 {
    font-size: 24px;
    line-height: 34px;
  }
}
.intro .container > p {
  padding-top: 36px;
}
@media (max-width: 1023px) {
  .intro .container > p {
    padding-top: 26px;
  }
}

.features {
  position: relative;
}
.features .container {
  padding-top: 120px;
  padding-bottom: 120px;
}
.features__content {
  position: relative;
  z-index: 1;
}
.features__content > h2 {
  text-align: center;
  margin: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
.features__list {
  display: grid;
  grid-template-columns: repeat(3, minmax(10px, 1fr));
  gap: 40px;
  margin-top: 60px;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .features__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: repeat(1, minmax(10px, 1fr));
  }
}
.features__item {
  padding: 30px 30px 50px;
  background-color: #f9fbfd;
}
.features__item h3 {
  margin-top: 25px;
  margin-bottom: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
.features__item p {
  margin-top: 20px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 20px;
}
.features__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.features__bg img {
  object-fit: cover;
  object-position: bottom;
  height: 100%;
  width: 100%;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.catalog {
