.catalog__item-content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 30px;
  height: 100%;
  color: #f9fbfd;
}
.catalog__item-content:hover, .catalog__item-content:focus, .catalog__item-content:focus-within {
  background-color: #011c40;
  padding: 30px 25px;
}
