  .catalog__item-content:hover, .catalog__item-content:focus, .catalog__item-content:focus-within {
    justify-content: space-between;
  }
}
.catalog__item-content:hover .catalog__item-details, .catalog__item-content:focus .catalog__item-details, .catalog__item-content:focus-within .catalog__item-details {
  display: grid;
  animation: fadeIn 1s;
