  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 20px;
  padding-left: 23px;
  cursor: pointer;
  transition: opacity 0.3s ease;
