  height: 20px;
  background-color: #f9fbfd;
  transition: opacity 0.3s ease, border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  position: absolute;
  top: 40%;
  left: 50%;
  opacity: 0;
  width: 6px;
  height: 10px;
  border-right: 1px solid #011c40;
  border-bottom: 1px solid #011c40;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: opacity 0.3s ease, border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #2c39f2;
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  content: "";
  opacity: 1;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
.custom-toggle .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
